<script>
export default {
  name: 'gSearch',
  data () {
    return {
      input: '',
      mode: '1',
      // 后台请求到的json数据
      data: require('../data/records.json'),
      results: []
    }
  },
  mounted () {
    this.$emit('getData', this.data)
  },
  methods: {
    createFilter (queryString) {
      return (res) => {
        return (res.value.toLowerCase().indexOf(queryString.toLowerCase()) !== -1)
      }
    }
  }
}
</script>

